@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
*,*::after,*::before{box-sizing:border-box;margin:0;padding:0}html,body,ul,h2,h3,h4{font-family:"Fira Sans",sans-serif,-apple-system,BlinkMacSystemFont;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:ivory;text-align:center}html h2,body h2,ul h2,h2 h2,h3 h2,h4 h2{font-size:3.5em;font-weight:700}html h3,body h3,ul h3,h2 h3,h3 h3,h4 h3{font-size:2em;font-weight:600}html h4,body h4,ul h4,h2 h4,h3 h4,h4 h4{font-size:.75em;font-weight:600}button{position:fixed;top:20px;left:20px;z-index:8}
